<template>
  <div class="container" :style="isStoreTheme&&{'background-color':'#482298'}">
    <div class="top" :class='{"store-theme":isStoreTheme,"normal-theme":!isStoreTheme}'></div>
    <div class="list" :class='{"store-theme":isStoreTheme,"normal-theme":!isStoreTheme}'>
      <!-- 无数据 -->
      <div class="item" :class='{"store-theme":isStoreTheme,"normal-theme":!isStoreTheme}' v-if='recordList.length===0'>
        <div class="bg">
          <div class="bg-top">
            <div class="title" style="visibility: hidden;"></div>
            <img style="width:0.8rem;height:0.96rem" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/game/no-data.png" alt="">
            <div class="name"></div>
            <div class="time">暂无中奖记录</div>
          </div>
          <div class="bg-middle">
            <div class="address"></div>
          </div>
          <div class="bg-bottom"></div>
        </div>
      </div>
      <template v-else>
        <div v-for="item in recordList" class="item" :class='{"store-theme":isStoreTheme,"normal-theme":!isStoreTheme}' :key='item.gamePrizeId'>
          <div class="bg">
            <div class="bg-top">
              <div class="title">我的奖品</div>
              <img :src="item.image" alt="">
              <div class="name">{{item.prizeName}}</div>
              <div class="time">{{item.time | formatDate}}</div>
            </div>
            <div class="bg-middle" >
              <div class="address" v-if='item.regionName&&item.address&&item.mobile'>收货地址：{{item.regionName+item.address+" "+item.consignee+" "+ item.mobile}}</div>
            </div>
            <div class="bg-bottom"></div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import {
  queryWinnerPrize,
} from '@/http/service/game'
export default {
  name: 'giftRecord',
  data() {
    return {
      isStoreTheme: false,
      recordList: [],
    }
  },
  created() {
    if (this.$route.query.isStore) {
      this.isStoreTheme = true
    }
    this.queryWinnerPrize();
  },
  methods: {
    queryWinnerPrize() {
      queryWinnerPrize({ gameId: this.$route.query.gameId }).then(res => {
        const data = res.data;
        if (data.success) {
          this.recordList = data.data;
        } else {
          this.$toast(data.message)
        }
      }).catch(err => {
        this.$$toast(err.message)
      })
    }
  }
}
</script>

