import http from '../http'
import url from '../url'

export const getGameInfo = (params) => {
  return http.request({
    url: url.getGameInfo,
    method: 'get',
    params
  })
}

export const gameLuckDraw = (params) => {
  return http.request({
    timeout: 6 * 1000,
    url: url.gameLuckDraw,
    method: 'get',
    params,
  })
}
export const gameAddAddress = (params) => {
  return http.request({
    url: url.gameAddAddress,
    headers: {
      'Content-Type': 'multipart/form-data'
    },
    method: 'post',
    params
  })
}
